import logo from '~/static/images/mobile-logo.png'
import brand from '~/static/text/brand'
import link from '~/static/text/link'
import ParallaxDeco from '../Parallax/Large'
import Hidden from '../Hidden'
import axios from 'axios'

export default {
  components: {
    Hidden,
    ParallaxDeco
  },
  data() {
    return {
      valid: true,
      snackbar: false,
      button: false,
      name: '',
      nameRules: [v => !!v || 'Name is required'],
      email: '',
      emailRules: [
        v => !!v || 'E-mail is required',
        v => /.+@.+\..+/.test(v) || 'E-mail must be valid'
      ],
      phone: '',
      company: '',
      message: '',
      checkbox: false,
      logo: logo,
      brand: brand,
      routeLink: link,
      username: 'classapp',
      api_key:
        '8957e5da070fca100834720c242b1c9f92f2cf5639053df7548bd01b306daf2f',
      recipient1: '0758513955',
      recipient2: '0778862475',
      recipient3: '0727802446'
    }
  },
  methods: {
    validate() {
      if (this.$refs.form.validate()) {
        // this.snackbar = true
      }
    },
    sendMessage() {
      this.button = true
      axios
        .post('https://contact-us-flask.herokuapp.com/api/v1/sms', {
          username: this.username,
          api_key: this.api_key,
          name: this.name,
          email: this.email,
          phone: this.phone,
          company: this.company,
          message: this.message,
          recipient1: this.recipient1,
          recipient2: this.recipient2,
          recipient3: this.recipient3
        })
        .then(Response => {
          this.button = false
          this.snackbar = true
          this.email = ''
          this.name = ''
          this.phone = ''
          this.company = ''
          this.message = ''
          console.log(Response)
        })
        .catch(err => {
          this.errors.push(err)
        })
    }
  },
  computed: {
    isMobile() {
      const smDown = this.$store.state.breakpoints.smDown
      return smDown.indexOf(this.$mq) > -1
    }
  }
}
